.room {
    padding: 6rem 0;
}

.room__title {
    max-width: 50rem;

    margin: 0 auto 3.5rem;

    text-align: center;
}


.room__gallery {
    display: grid;
    justify-content: center;
    align-items: center;
    gap: 4.5rem;
    grid-template-columns: repeat(3, 34rem);
}

.room__img {
    display: block;

    width: 34rem;
    height: 43rem;
    background: aqua;

    &:hover {

        transform: scale(1.1);
    }
}